html,
body,
.app {
  height: 100vh;
  padding: 1em;
  background-color: #eee;
}
/* sidepanel */
.menu {
  padding-bottom: 2em;
}

.messages {
  height: 62vh;
  overflow-y: scroll;
}
.messages_progress {
  height: 58vh;
  overflow-y: scroll;
}
.message__form {
  position: fixed !important;
  bottom: 1em;
  margin-left: 320px !important;
  left: 0;
  right: 1em;
  z-index: 200;
}
.message_self {
  border-left: 2px solid blue;
  padding-left: 8px;
}
.message_image {
  padding: 1em;
}
.progress_bar {
  margin: 0.3em 0 0 0 !important;
}
